.popup-filter {
  position: absolute;
  top: 100%;
  left: 0;
  background: #fff;
  border: 1px solid #eaeaea;
  border-radius: 6px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  z-index: 10;
  min-width: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.popup-filter-arrow-wrap {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.popup-filter-arrow {
  width: 16px;
  height: 16px;
  padding: 10px;
  cursor: pointer;
  background-color: white;
  border-radius: 4px;
  transition: background 0.15s;
}

.popup-filter-arrow.asc-hover {
  background-color: #f0f8ff;
}

.popup-filter-arrow.desc-hover {
  background-color: #f0f8ff;
}

.popup-filter-tooltip {
  position: absolute;
  top: 0;
  left: 110%;
  background: #fff;
  border: 1px solid #eaeaea;
  border-radius: 4px;
  padding: 2px 8px;
  font-size: 12px;
  color: #333;
  white-space: nowrap;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
} 