  .app {
  min-height: 100vh;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

table {
  width: 100%;
  max-width: 1400px;
  margin: 40px;
  border-collapse: collapse;
  font-family: 'Segoe UI', Arial, sans-serif;
  background: #fff;
  box-shadow: 0 2px 16px rgba(0,0,0,0.07);
  border-radius: 10px;
  overflow: hidden;
  border: 2px solid #e0e0e0;
  table-layout: fixed;
}

thead {
  background: #f7f7f7;
}

th, td {
  padding: 8px 12px;
  box-sizing: border-box;
  text-align: left;
  border-right: 1px solid #e0e0e0;
  position: relative;
  text-overflow: ellipsis;
  white-space: nowrap;
  height: 8vh;
}

th:last-child, td:last-child {
  border-right: none;
}

th {
  font-weight: 600;
  letter-spacing: 0.5px;
  color: #333;
  border-bottom: 2px solid #eaeaea;
  min-width: 100px;
}

tbody tr {
  border-bottom: 1px solid #f0f0f0;
  transition: background 0.2s;
}

tbody tr:hover {
  background: #f5faff;
}

tbody tr:last-child {
  border-bottom: none;
}

td {
  color: #444;
  height: 6vh;
}

th[data-label="Email"], td[data-label="Email"] {
  min-width: 160px;
  word-break: break-all;
}

th[data-label="Страна"], td[data-label="Страна"] {
  min-width: 100px;
}

/* Если таблица слишком широкая, добавим горизонтальный скролл */
.app table {
  width: 100%;
  border-collapse: collapse;
  table-layout: auto;
}

.app {
  overflow-x: auto;
}

@media (max-width: 800px) {
  table, thead, tbody, th, td, tr {
    display: block;
  }
  th, td {
    padding: 10px 8px;
    border-right: none;
  }
  thead {
    display: none;
  }
  tr {
    margin-bottom: 15px;
    box-shadow: 0 1px 4px rgba(0,0,0,0.04);
    border-radius: 8px;
    background: #fff;
    border: 1px solid #e0e0e0;
  }
  td {
    position: relative;
    padding-left: 50%;
    min-height: 40px;
    border: none;
    border-bottom: 1px solid #f0f0f0;
  }
  td:before {
    position: absolute;
    left: 12px;
    top: 12px;
    white-space: nowrap;
    font-weight: 600;
    color: #888;
    content: attr(data-label);
  }
  .column-resizer {
    display: none;
  }
}



.name-filter-popup {
  position: absolute;
  top: 28px;
  left: 0;
  background: #fff;
  border: 1px solid #e0e0e0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
  padding: 10px 12px;
  border-radius: 6px;
  z-index: 10;
  min-width: 180px;
  display: flex;
  align-items: center;
}

.name-filter-popup input {
  width: 90px;
}

.name-filter-popup button[type="submit"] {
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 3px;
  padding: 2px 10px;
  cursor: pointer;
}

.name-filter-popup button[type="button"] {
  color: #888;
  font-size: 18px;
  margin-left: 2px;
  background: none;
  border: none;
  cursor: pointer;
}